export default {
  namespaced: true,
  state: () => ({
    user: { name: "saeed", id: 1 },
    isLoggedIn: true
  }),
  getters: {
    name: (state, ...otherProps) => {
      // console.log(otherProps);
      return state.user.name;
    }
  },
  mutations: {
    SET_USER(state, payload) {
      state.user.name = "mohammad";
    }
  },
  actions: {
    userLog(ctx, payload) {
      console.log(payload);
    }
  }
};
